import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { AssociationChangeRecordComponent } from "./association-change-record/association-change-record.component";
import { AssociationRelationAddComponent } from "./association-relation-add/association-relation-add.component";
import { AssociationRelationDelComponent } from "./association-relation-del/association-relation-del.component";
import { AssociationRelationListComponent } from "./association-relation-list/association-relation-list.component";
import { AssociationRelationModifyComponent } from "./association-relation-modify/association-relation-modify.component";

const routes: Routes = [
    {
        path: '',
        children: [
                { path: 'association-change-record', component: AssociationChangeRecordComponent},
                {path: 'association-relation-add', component: AssociationRelationAddComponent},
                {path: 'association-relation-del', component: AssociationRelationDelComponent},
                {path: 'association-relation-list', component: AssociationRelationListComponent},
                {path: 'association-relation-modify', component: AssociationRelationModifyComponent},
                {path: '', redirectTo: 'association-change-record', pathMatch: 'full'}
        ]
    }
];
export const AssociationRelationManagerRouting: ModuleWithProviders = RouterModule.forChild(routes);