import { Component, OnInit } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Message } from 'primeng/primeng';
import {MessageService} from 'primeng/components/common/messageservice';
import * as moment from "moment";
// import { ReductionStatus, AsociationStatus,DomPanduan, ReductionNumberTypeShow } from '../../../../constant/custView.codeValue';
// import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
@Component({
  selector: 'app-association-change-record',
  templateUrl: './association-change-record.component.html',
  styleUrls: ['./association-change-record.component.scss'],
  providers: [Commfunc,MessageService]

})
export class AssociationChangeRecordComponent implements OnInit {

  title:string='关联关系变更记录';

  // dataTable数据
  items: any[];

  // 开始日期
  startDate: any;

  // 结束日期
  endDate: any;
  
  //用户号
  pepCode: string;

  // 状态
  statusId: string;
  total;
  // 页数
  public pageSize: number;
  public pageNum: number;
  // public asociationStatusOptions :any = ReductionStatus;
  // public reductionNumberType :any = ReductionNumberTypeShow;
  // public panduan :any = DomPanduan;
  //提示信息
  public msgs: Message[] = [];

  constructor(
    public messageService: MessageService,
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
  ) { }

  ngOnInit() {
    this.statusId = 'all';
    this.pageSize = 10;
    this.pageNum = 1;
    // this.items=[
      //   {
      //     custNo:1300000001,
      //     custName:'王小明',
      //     relationshipCustNo:1300000002,
      //     relationshipCustName:'王二明',
      //     relationshipType:'家庭关系',
      //     f:'兄弟',
      //     whetherReductionNumber:'是',
      //     h:'客户',
      //     i:'台州银行洪家支行',
      //     statusId:'已建立',
      //     k:'新建',
      //     l:'2017/08/08'
      //   },
      //   {
      //     custNo:1300000001,
      //     custName:'王小明',
      //     relationshipCustNo:1300000002,
      //     relationshipCustName:'王二明',
      //     relationshipType:'家庭关系',
      //     f:'兄弟',
      //     whetherReductionNumber:'是',
      //     h:'客户',
      //     i:'台州银行洪家支行',
      //     statusId:'已建立',
      //     k:'新建',
      //     l:'2017/08/08'
      //   }
    // ]
    this.queryClick()
  }

  // 数据查询
  queryClick() {
    let param = {
      "pageNum": this.pageNum,
      "pageSize": this.pageSize,
      // "statusId": this.statusId,
      "custNo": this.pepCode || "1000000036",
      "lastStartDate": this.commfunc.transDateN(this.startDate),
      "lastEndDate": this.commfunc.transDateN(this.endDate)
    }
     //日期
    //  this.postsManageBean.introductionDate = this.commfunc.transDateN(this.postsManageBean.introductionDate) + ' 00:00:00';
    //  this.postsManageBean.releaseDate = this.commfunc.transDateN(this.postsManageBean.releaseDate);

    this.httpService.selectPartyRelationship(param).subscribe((data) => {
      if(data.returnCode.code == "success") {
        this.items = [];
        data.partyRelationshipList.forEach((v) => {
          this.items.push(v)
        })
        this.msgs = [];
        this.msgs.push({ severity: "success", summary: "提示", detail: "查询成功" })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: "fail", summary: "提示", detail: "查询失败" })
      }
    },
    error => {
      this.msgs = [];
      this.msgs.push({ severity: "error", summary: "错误", detail: "调用服务失败" })
    })
  }

  // 查询条件重置
  restClick() {
    this.pepCode = "";
    this.startDate = "";
    this.endDate = "";
    this.queryClick()
  }

  // 分页功能
  paginate(event) {
    this.pageSize = event.rows;
    this.pageNum = event.page + 1;
    //             rows:10       pageCount: 1
    this.queryClick();
    // this.pageNum = 1;
  }
}
