import { Component, OnInit,EventEmitter,Output} from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { SelectPartyRelationshipBean } from '../bean/selectPartyRelationship.bean';
// import { Panduan,DomPanduan, RelationshipType, ParentTypeId, StatusId, ReductionStatus,ReductionNumberTypeShow } from '../../../../constant/custView.codeValue';
import { ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { AddPartyRelationshipBean } from '../../../../custom-view/association-relation/personal-relation/bean/addPartyRelationship.bean';
@Component({
  selector: 'app-association-relation-list',
  templateUrl: './association-relation-list.component.html',
  styleUrls: ['./association-relation-list.component.scss'],
  providers: [CusViewHttpService, ConfirmationService, Commfunc]
})
export class AssociationRelationListComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  title: string = "关联关系查询";
  cities: SelectItem[];
  selectedCity: string;
  items: any;
  public detailDisplay: boolean = false;//关联个人详情
  public detailValue: any;
  // public comDetailDisplay: boolean = false;//企业详情
  public panduan: any = [];
  public domPanduan: any = [];
  public reductionNumberType: any[] = [];
  // public relationshipType: any = RelationshipType;
  public parentId: any = [];
  public parentTypeId: string;//父级关系类型编号
  public relationshipType: string;//子级关系类型编号
  public relationship: any = [];
  // public parentTypeId: any = ParentTypeId;
  public statusId: any = [];
  public reductionStatus: any = [];
  //模态框的标题
  public headerTitle: string;
  //模态框是否显示
  public display: boolean = false;
  public showModel: string;
  //修改的参数
  public updateValue: any = [];
  //总页数
  public total: number;
  //每页多少条
  public pageSize: number = 10;
  public pageNum: number = 1;
  public createStartDate = '';
  public createEndDate = '';
  public lastStartDate = '';
  public lastEndDate = '';
  //提示信息
  public msgs: Message[] = [];
  public tabMesg = tableMessage;
  public addPartyRelationshipBean: AddPartyRelationshipBean = new AddPartyRelationshipBean();
  public ch: any = CALENDAR_CN;
  public selectPartyRelationshipBean: SelectPartyRelationshipBean = new SelectPartyRelationshipBean();
  outVal:any;
  waitSelUserList:any;
  aaaa:boolean=false;
  outValBean:any;
  code:any;
  constructor(
    public cusViewHttpService: CusViewHttpService,
    public confirmationService: ConfirmationService,
    public startTime: Commfunc,
    public router: Router,
    public commfunc: Commfunc,
    public httpService: CusViewHttpService,
  ) {
    this.codeVal();
    this.panduan=this.code['Panduan'];
    this.domPanduan=this.code['DomPanduan'];
    this.reductionNumberType=this.code['ReductionNumberTypeShow'];
    this.statusId=this.code['StatusId'];
    this.reductionStatus=this.code['ReductionStatus'];
   }

  ngOnInit() {
    this.selectPartyRelationshipBean.pageSize = 10;
    this.selectPartyRelationshipBean.pageNum = 1;
    //关系类型，子关系类型
    let param = { relationshipType: this.relationshipType, parentTypeId: this.parentTypeId, queryType: '1' }
    let ob = this.cusViewHttpService.selectPartyRelationshipType(param);
    ob.subscribe(data => {
      let items = data.partyRelationshipTypeList;
      this.parentId = this.startTime.toChange(items, 'relationshipName', 'relationshipType');
      this.parentId.unshift({ label: '请选择', value: '' }, )
    })
    this.query();
  }
  //重置按钮
  reset() {
    this.createStartDate = null;
    this.createEndDate = null;
    this.lastStartDate = null;
    this.lastEndDate = null;
    this.selectPartyRelationshipBean = new SelectPartyRelationshipBean();
    this.selectPartyRelationshipBean.pageSize = 10;
    this.selectPartyRelationshipBean.pageNum = 1;
  }
  //查询按钮
  query() {
    if (this.createStartDate) {
      this.selectPartyRelationshipBean.createStartDate = this.startTime.transDateN(this.createStartDate);
    }
    if (this.createEndDate) {
      this.selectPartyRelationshipBean.createEndDate = this.startTime.transDateN(this.createEndDate);
    }
    if (this.lastStartDate) {
      this.selectPartyRelationshipBean.lastStartDate = this.startTime.transDateN(this.lastStartDate);
    }
    if (this.lastEndDate) {
      this.selectPartyRelationshipBean.lastEndDate = this.startTime.transDateN(this.lastEndDate);
    }
    let ob = this.cusViewHttpService.selectPartyRelationship(this.selectPartyRelationshipBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.partyRelationshipList;
        this.total = data.totalNum;
        // this.msgs.push({ severity: 'success', summary: '提示', detail:data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //联动
  doSelet() {
      let param = { parentTypeId: this.selectPartyRelationshipBean.parentTypeId, queryType: '2', relationshipType: this.selectPartyRelationshipBean.relationshipType }
      let ob = this.cusViewHttpService.selectPartyRelationshipType(param);
      ob.subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.relationship = [];
          data.partyRelationshipTypeList = this.startTime.toChange(data.partyRelationshipTypeList, 'relationshipName', 'relationshipType');
          data.partyRelationshipTypeList.unshift({ label: '请选择', value: '' });
          this.relationship = data.partyRelationshipTypeList;
        }
      })
  }

    //跳转到个人视图
    toCustView(data) {
      this.startTime.setSessionData('custNo', data.relationshipCustNo);
      this.router.navigate(['pages/custom-view']).then(item => {
        // this.globalState.notifyDataChanged('event:custom-view-refresh',{'custNo': data.relationshipCustNo});
        window.location.reload();
      });
    }

  //新增按钮事件
  addClick() {
    this.headerTitle = '关联关系新增';
    this.display = true;
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    if(param==false){
      this.display = false;
      this.selectPartyRelationshipBean.pageNum = 1;
      this.query();
      if(param!='取消'){
        this.msgs=[{ severity: 'success', summary: '提示', detail: '新增成功' }];    
      }
    }else{
      console.log(param);
      this.outVal=param.outVal;
      this.aaaa=param.aaaa;
    }
    // this.display = false;
    // this.selectPartyRelationshipBean.pageNum = 1;
    // this.query();
    // if(param!='取消'){
    //   this.msgs=[{ severity: 'success', summary: '提示', detail: '新增成功' }];    
    // }
  }

  // 详情
  detail(car) {
    // let param = {
    //   custNo: car.relationshipCustNo
    // }
    // this.cusViewHttpService.selectCustBriefInfo(param).subscribe(data => {
    //   if (data.returnCode.code == 'success') {
    //     // if (data.custType == '1') {
    //       this.detailDisplay = true;
    //       this.detailValue = _.clone(car);
    //     // } else if (data.custType == '2') {
    //     //   this.comDetailDisplay = true;
    //     //   this.detailValue = _.clone(car);
    //     // }
    //   }
    // })
    this.detailDisplay = true;
    this.detailValue = _.clone(car);
  }

  //详情的回调
  detailsCall(param) {
    // this.comDetailDisplay = param;
    this.detailDisplay = param;
    this.query();
    document.body.style.overflow = 'auto';
  }

  //修改按钮事件
  modifyClick(item) {
    this.headerTitle = '关联关系修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue =_.clone(item);
  }

  //修改的回调
  updateCall(param) {
    if(param==false){
      this.display = false;
      this.selectPartyRelationshipBean.pageNum = 1;
      this.query();
      this.msgs=[{ severity: 'success', summary: '提示', detail: '修改成功' }];
    }else if(param=='取消'){
      this.display = false;
    }else{
      console.log(param);
      this.outVal=param.outVal;
      this.aaaa=param.aaaa;
    }
  }

  //删除按钮事件
  deleteClick(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        this.waitSelUserList=this.commfunc.query('301000',{relationshipType:this.addPartyRelationshipBean.relationshipType});
        let outVal=this.waitSelUserList.waitSelUserList;
        let aaaa=this.waitSelUserList.display;
        console.log(this.aaaa);
        let commonHeader=JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let a=commonHeader.extParam.specifyNextOpers;
        console.log(a);
        if(this.aaaa==true&&(a==undefined||a==''||a==null)){
          // this.outVal.emit({outVal:this.outVal,aaaa:this.aaaa});
          this.outVal=outVal;
          this.aaaa=aaaa;      
        }else{
          let ob = this.httpService.deletePartyRelationship(param);
          ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
              let s=JSON.parse(this.commfunc.getSessionData('commonHeader'));
              if(s.extParam.specifyNextOpers){
                delete s.extParam.specifyNextOpers
                let commonHeader=s;
                this.commfunc.setSessionData('commonHeader',JSON.stringify(commonHeader));  
              }
              // this.router.navigate(['pages/tzb/workflow/custom-workflow/association-relation', data.dataMap]);
              this.outValue.emit(false);
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
              this.addPartyRelationshipBean = new AddPartyRelationshipBean();
            } else {
              let s=JSON.parse(this.commfunc.getSessionData('commonHeader'));
              if(s.extParam.specifyNextOpers){
                delete s.extParam.specifyNextOpers
                let commonHeader=s;
                this.commfunc.setSessionData('commonHeader',JSON.stringify(commonHeader));  
              }
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
          }, error => {
            let s=JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if(s.extParam.specifyNextOpers){
              delete s.extParam.specifyNextOpers
              let commonHeader=s;
              this.commfunc.setSessionData('commonHeader',JSON.stringify(commonHeader));  
            }
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
          })
        }
      }
    })
        //条件
        let param = { custNo: car.custNo, relationshipCustNo: car.relationshipCustNo, relationshipType: car.relationshipType, weakIdenFlag: car.weakIdenFlag };
        this.cusViewHttpService.deletePartyRelationship(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            let param = data.dataMap;
            // this.router.navigate(['pages/tzb/workflow/custom-workflow/association-relation',param]);
            this.msgs = [];
            this.query();
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
       
    
  }
  deleteCall(param) {
    if(param==false){
      this.display = false;
      this.selectPartyRelationshipBean.pageNum = 1;
      this.query();
      if(param!='取消'){
        this.msgs=[{ severity: 'success', summary: '提示', detail: '删除成功' }];    
      }
    }else{
      console.log(param);
      this.outVal=param.outVal;
      this.aaaa=param.aaaa;
    }
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.selectPartyRelationshipBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.selectPartyRelationshipBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.selectPartyRelationshipBean.pageNum = 1;
  }

  outDisplayCall(event){
    console.log(event);
    this.aaaa=event;
    this.display=false;
  }
  outValueCall(event){
    console.log(event);
    this.outVal=event;
    let ss='';
    this.outVal.forEach(item => {
      ss+=item.tellerId+','
    });
    let commonHeader=JSON.parse(this.startTime.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers']=ss;
    console.log(commonHeader);
    this.startTime.setSessionData('commonHeader',JSON.stringify(commonHeader));
    this.cusViewHttpService.addPartyRelationship(this.outValBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s=JSON.parse(this.startTime.getSessionData('commonHeader'));
        if(s.extParam.specifyNextOpers){
          delete s.extParam.specifyNextOpers
          let commonHeader=s;
          this.startTime.setSessionData('commonHeader',JSON.stringify(commonHeader));  
        }
        this.aaaa=false;
        this.display=false;
        // this.outValue.emit(false);
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        // this.addPartyRelationshipBean = new AddPartyRelationshipBean();
      } else {
        let s=JSON.parse(this.startTime.getSessionData('commonHeader'));
        if(s.extParam.specifyNextOpers){
          delete s.extParam.specifyNextOpers
          let commonHeader=s;
          this.startTime.setSessionData('commonHeader',JSON.stringify(commonHeader));  
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      let s=JSON.parse(this.startTime.getSessionData('commonHeader'));
      if(s.extParam.specifyNextOpers){
        delete s.extParam.specifyNextOpers
        let commonHeader=s;
        this.startTime.setSessionData('commonHeader',JSON.stringify(commonHeader));  
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }


  addCallBean(event){
    this.outValBean=event;
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
